//Створення розмітки елементів

const galleryEl = document.querySelector('.gallery');

Object.assign(galleryEl.style, {
  display: 'flex',
  flexDirection: 'row',
  gap: '24px',
  flexWrap: 'wrap',
  justifyContent: 'center',
  margin: '24px',
});

//Зверни увагу на те, що зображення огорнуте посиланням,
// у якого атрибут href вказує на шлях до файлу з зображенням.
//  Отже клік по ньому може викликати завантаження зображення на
//  комп’ютер користувача. Заборони цю поведінку за замовчуванням.

galleryEl.addEventListener('click', handleLinkClick);

function handleLinkClick(e) {
  if (!e.target.closest('.gallery-link')) {
    return;
  }

  e.preventDefault();
}

//продовжуєм створювати розмітку (за допомогою 3 функцій)

function imageTemplate(image) {
  return `<li class="gallery-item">
  <a class="gallery-link" href="${image.original}">
    <img
      class="gallery-image"
      src="${image.preview}"
      alt="${image.description}"
    />
  </a>
</li>`;
}

function imagesTemplate(images) {
  return images.map(imageTemplate).join('');
}

function renderImages() {
  const markup = imagesTemplate(images);
  galleryEl.innerHTML = markup;
}

renderImages();

// Ініціалізація SimpleLightbox з опціями
const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt', // Брати підпис з атрибуту alt
  captionDelay: 250, // Затримка перед показом підпису
  captionPosition: 'bottom', // Підпис внизу
});

//images styles

const imageEls = document.querySelectorAll('.gallery-image');

imageEls.forEach(img => {
  Object.assign(img.style, {
    width: '360px',
    height: '100%',
  });
});
